.portfolio {
  padding-block: 60px;

  &__title {
    margin-bottom: 40px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__card {
    display: flex;
    justify-content: space-between;
    min-height: 400px;
    padding: 35px 35px;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-small);
  }
}
