.header {
  top: -70px;
  left: 0;
  width: 100%;
  height: 70px;
  padding-inline: 231.5px;
  transition: all 0.2s;

  &__logo {
    width: 15%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
  }

  &__container {
    height: 100%;
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__nav {
    margin-left: -13%;
  }

  &__nav-list {
    display: flex;
  }

  &__nav-item {
    font-size: 20px;
    font-weight: 300;

    &:not(:last-child) {
      margin-right: 25px;
    }

    &:hover {
      color: var(--color-accent);
    }
  }
}

.header.is-scrolled {
  position: fixed;
  z-index: 10;
  background-color: #1a1a1d;
  box-shadow: var(--box-shadow);
  transform: translateY(70px);
}
