.exp {
  &__card {
    display: flex;
    flex-direction: column;
    padding: 30px;
    width: 100%;
    min-height: 400px;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-small);

    &:hover {
      box-shadow: var(--box-shadow-inset-2);
    }
  }

  &__title {
    order: 2;
    margin-bottom: 20px;
    font-size: 35px;
  }

  &__date {
    order: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-accent);
    width: 20%;
    min-height: 60px;
    margin-bottom: 25px;
    border: 1px solid transparent;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-inset-2);

    transition: all 0.2s;

    &:hover {
      border: 1px solid var(--color-accent);
    }
  }

  &__subTitle {
    order: 3;
    margin-bottom: 10px;
    font-size: 30px;
  }

  &__body {
    order: 4;
    margin-bottom: 30px;
  }

  &__tech {
    order: 5;
    display: flex;
  }

  &__icon {
    width: 70px;
    height: 70px;
    padding: 15px;
    border: 1px solid transparent;
    border-radius: 50%;
    box-shadow: var(--box-shadow-icons);

    transition: 0.2s;

    &:not(:last-child) {
      margin-right: 20px;
    }

    &:hover {
      border: 1px solid var(--color-accent);
    }
  }
}
