.card {
  transition: 0.3s;
  &:hover {
    box-shadow: var(--box-shadow-inset-2);
  }

  &__link {
    display: inline-block;
    width: 35%;
  }

  &__img {
    height: 100%;
    border-radius: 5px;
  }

  &__body {
    width: 50%;
    height: 100%;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__descr {
    margin-bottom: 40px;
  }

  &__stack {
    display: flex;

    &-img {
      width: 70px;
      height: 70px;
      padding: 15px;
      border: 1px solid transparent;
      border-radius: 50%;
      box-shadow: var(--box-shadow-icons);

      transition: 0.2s;

      &:not(:last-child) {
        margin-right: 20px;
      }

      &:hover {
        border: 1px solid var(--color-accent);
      }
    }
  }
}
